.main-content {
    width: 1000px;
    min-height: 400px;
    height: 700px;
    padding: 18px 18px 120px 18px;
    position: relative;
    resize: none;
    box-shadow: 0 12px 48px 0px rgb(109 117 141 / 20%);
    background: rgb(255 255 255);
    border: 0.5px solid rgb(231 233 245);
    border-radius: 16px;
    font-size: 18px;
    letter-spacing: 1px;
    word-spacing: 2px;
    line-height: 1.6;
}
  
.main-content:focus {
    border: 0.3px solid rgb(43, 45, 56, 0.3);
    outline: none;
}

.btn-button {
    /* position: absolute; */
    background: rgb(17 166 131);
    color: rgb(255 255 255);
    border-radius: 6px;
    padding: 16px 32px;
    border: none;
    font-size: 18px;
    letter-spacing: 1px;
    margin: auto;
    display: flex;
    position: relative;
    margin-top: -80px;
    cursor: pointer;
}
  
.btn-button:focus,
.btn-button:hover {
    background: rgb(42, 201, 164);
    box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.1);
}
  
.btn-button+.btn-button {
    margin-left: 30px
}